html,
body {
  overflow-x: hidden;
}
:root {
  --card-height: 300px;
  --card-width: 100%;
}

.card {
  width: var(--card-width);
  height: var(--card-height);
  position: relative;
  display: flex;
  justify-content: center;
  align-items: flex-end;
  perspective: 2500px;
}

.cover-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.wrapper {
  transition: all 0.5s;
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: -1;
  border-radius: 12px;
  overflow: hidden;
}

.card-content {
  width: 100%;
  height: 100%;
  position: absolute;
  z-index: 2;
}

.card:hover .wrapper {
  transform: perspective(900px) translateY(-5%) rotateX(25deg) translateZ(0);
  box-shadow: 2px 35px 32px -8px rgba(0, 0, 0, 0.75);
  -webkit-box-shadow: 2px 35px 32px -8px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 2px 35px 32px -8px rgba(0, 0, 0, 0.75);
}

.wrapper::before,
.wrapper::after {
  content: '';
  opacity: 0;
  width: 100%;
  height: 80px;
  transition: all 0.5s;
  position: absolute;
  left: 0;
}
.wrapper::before {
  top: 0;
  height: 100%;
  background-image: linear-gradient(
    to top,
    transparent 46%,
    rgba(12, 13, 19, 0.5) 68%,
    rgba(12, 13, 19) 97%
  );
}
.wrapper::after {
  bottom: 0;
  opacity: 1;
  background-image: linear-gradient(
    to bottom,
    transparent 46%,
    rgba(12, 13, 19, 0.5) 68%,
    rgba(12, 13, 19) 97%
  );
}

.card:hover .wrapper::before,
.wrapper::after {
  opacity: 1;
}

.card:hover .wrapper::after {
  height: 120px;
}
.title {
  width: 100%;
  transition: all 0.5s;
  text-align: center;
  padding-bottom: 12px;
  color: rgb(255, 255, 255);
  font-weight: 600;
  font-size: 18px !important;
}
.card:hover .title {
  display: none;
}

.character {
  width: 100%;
  height: 80%;
  opacity: 0;
  transition: all 0.5s;
  position: absolute;
  z-index: -1;
}

.button-detail {
  display: none !important;
  color: #fff !important;
  background-color: rgb(57, 99, 157) !important;
  transition: display 0.5s ease-in, transform 10s ease-in-out;
  padding: 0px 8px;
  border-radius: 4px;
  text-decoration: none;
}

.switch {
  width: 120%;
  height: 80%;
  opacity: 1;
  transition: all 0.5s;
  position: absolute;
  left: 8px;
  top: 20px;
}

.card:hover .character {
  opacity: 1;
  transform: translate3d(0%, -40%, 100px);
}
.card:hover .switch {
  opacity: 1;
  left: 0px;
  transform: translate3d(4%, -1%, 100px);
}

.card:hover .button-detail {
  display: block !important;
  transform: translate3d(0%, -70%, 100px);
}

.detail {
  position: absolute;
  top: 0;
}

@keyframes zoom-in {
  from {
    transform: scale(1) translate(-0, -100px);
    color: red;
  }
  to {
    transform: scale(1.5) translate(-0, -100px);
    color: red;
  }
}
$color-primary-white: rgb(61, 61, 157);

.game-detail {
  width: 100%;
  height: 100%;
  overflow: hidden;
  border-radius: 22%;
  box-shadow: rgba(0, 0, 0, 0.25) 0px 14px 28px,
    rgba(0, 0, 0, 0.22) 0px 10px 10px;
  transform: all 0.5s ease;
  .card {
    width: 100% !important;
    height: 100% !important;

    position: relative;
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      max-width: 100%;
      max-height: 100%;
      opacity: 0.9;
      transition: opacity 0.2s ease-out;
    }

    .game-title {
      position: absolute;
      inset: auto auto 16px 16px;
      margin: 0;
      transition: inset 0.3s 0.3s ease-out;
      text-transform: uppercase;
      font-weight: 600;
      text-overflow: ellipsis;
      white-space: nowrap;
      overflow: hidden;
      color: #ffffff;
      text-shadow: 1px 1px 2px red, 0 0 1em blue, 0 0 0.2em blue;
      font-weight: 600;
      font-size: 18px;
      width: 80%;
      padding: 2px 4px;
    }

    p,
    a {
      position: absolute;
      opacity: 0;
      max-width: 80%;
      transition: opacity 0.3s ease-out;
      margin: 0;
      padding: 16px;
    }

    p {
      width: 100%;
      max-width: 100%;
      inset: auto auto 00px 00px;
    }

    &:hover .game-title {
      inset: auto auto 40% 12px;
      transition: inset 0.3s ease-out;
      overflow-wrap: break-word;
      word-wrap: break-word;
      hyphens: auto;
      white-space: wrap;
    }

    &:hover p,
    &:hover a {
      opacity: 1;
      transition: opacity 0.5s 0.1s ease-in;
    }

    &:hover img {
      transition: opacity 0.3s ease-in;
      opacity: 1;
    }
  }

  .material-symbols-outlined {
    vertical-align: middle;
  }

  &:hover .override-circle {
    transform: scale(10);
  }

  .override-circle {
    position: absolute;
    overflow: hidden;
    top: -75px;
    right: -75px;
    width: 128px;
    height: 128px;
    transition: transform 0.5s ease-in;
    border-radius: 22%;
    .circle {
      width: 100%;
      height: 100%;
      border-radius: 50%;
    }
  }
}

.button-85 {
  padding: 0.6em 2em;
  border: none;
  outline: none;
  color: rgb(255, 255, 255);
  background: #111;
  cursor: pointer;
  position: relative;
  z-index: 0;
  border-radius: 10px;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
  width: 100%;
  font-size: 18px;
}

.button-85:before {
  content: '';
  background: linear-gradient(
    45deg,
    #ff0000,
    #ff7300,
    #fffb00,
    #48ff00,
    #00ffd5,
    #002bff,
    #7a00ff,
    #ff00c8,
    #ff0000
  );
  position: absolute;
  top: -2px;
  left: -2px;
  background-size: 400%;
  z-index: -1;
  filter: blur(5px);
  -webkit-filter: blur(5px);
  width: calc(100% + 4px);
  height: calc(100% + 4px);
  animation: glowing-button-85 20s linear infinite;
  transition: opacity 0.3s ease-in-out;
  border-radius: 10px;
}

@keyframes glowing-button-85 {
  0% {
    background-position: 0 0;
  }
  50% {
    background-position: 400% 0;
  }
  100% {
    background-position: 0 0;
  }
}

.button-85:after {
  z-index: -1;
  content: '';
  position: absolute;
  width: 100%;
  height: 100%;
  background: #222;
  left: 0;
  top: 0;
  border-radius: 10px;
}
